@import url(./font.css);
/* Estilos para el componente "dot" */
.dot {
  z-index: 100 !important;
  display: flex;
  width: fit-content;
  height: auto;
  pointer-events: none;
  font-family: Monzerrat-Regular;
}

.dot.alternative {
  display: grid;
}

/* Estilos para el contenedor de contenido */
.dot div.content {
  display: none;
  opacity: 0;
}
.dot .hidden-section {
  opacity: 0 !important;
  transition: opacity 2s !important;
}

div.active-section {
  display: flex !important;
  z-index: -10 !important;
  opacity: 1 !important;
  transition: opacity 2s !important;
}

.dot .content {
  display: flex;
  width: 60vw;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}
.dot .content .sub-title {
  margin: 0;
  font-family: Monzerrat-Regular;
  font-size: 10px;
}
.dot .content .title {
  grid-area: title;
  margin-bottom: 20px;
  font-size: 40px;
  background: linear-gradient(270deg, #747474 50%, #fff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  filter: brightness(2);
}
.dot .content .description {
  grid-area: description;
  margin-bottom: 25px;
  padding: 0 120px;
  color: rgba(255, 255, 255, 0.69);
  text-shadow: 0px 4px 5px 0px rgba(0, 0, 0, 0.25);
  font-family: Monzerrat-Regular;
  line-height: 15px;
  font-size: 10px;
}

/*estilos para las listas*/
.dot .list-container {
  grid-area: list;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin: auto;
  margin-bottom: 30px;
}

/* Estilos para botones subpage */
.btn-sub-page-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  pointer-events: all;
  cursor: pointer;
  z-index: 110;
}
.btn-sub-page-container span {
  pointer-events: none;
  z-index: 50;
  font-size: 11px;
  margin-right: -5px;
}

/*estilos para botones general*/

.dot-button {
  width: fit-content;
  height: 30px;
  font-size: 10px;
  padding: 0 8px;
  padding: 5px 10px;
  background-color: unset;
  border: 2px solid rgb(255, 255, 255);
  border-radius: 2rem;
  color: white;
  filter: brightness(3);
  cursor: pointer;
  display: block;
  margin: 0 15px;
  transition: transform 0.3s;
  transition: opacity 0.3;
  list-style: none;
  text-decoration: unset;
  text-decoration-line: none !important;
}

.dot-button:hover {
  transform: scale(1.1);
  transition: transform 0.3s;
}
/* Disminuir opacidad del resto de elementos cuando uno está siendo hover */
/* Estilo para los elementos hijos que NO están en hover */
.list-container .dot-button:not(:hover) {
  opacity: 0.5; /* Opacidad menor para los elementos NO en hover */
}
.dot-button.round {
  height: 30px;
  width: 30px;
  padding: 0;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.dot-button.round img {
  width: 80%;
  height: 80%;
  object-fit: contain;
  filter: brightness(2);
  transform: rotate(90deg);
  pointer-events: none;
}

/*estilos para imagenes de muestra*/
.dot .alternative {
  width: 50vw;
  text-align: left;
  display: grid !important;
  grid-template-rows: repeat(3, auto);
  grid-template-areas:
    "img title"
    "img description"
    "img list";
}

.dot .ntfs-section.active-section div{
  display: grid;
  grid-template-rows: repeat(3, auto);
  grid-template-areas:
    "title img"
    "description img"
    "list img";
}
.dot .ntfs-section.active-section .img-preview {
  grid-area: img;
  display: flex;
  justify-content: center;
  align-items: center;
  width:  280px;
  height: 280px;
  margin-right: 35px;
  position: relative; /* Añadir posición relativa para que la imagen pueda ajustarse correctamente */
}
.dot .ntfs-section.active-section .img-preview img{
  object-fit: contain; 
  width: 100%;
  height: 100%;
}
.dot .ntfs-section.active-section .title {
  font-size: 40px;
  margin-left: 5px;
  width: calc(100% / 1.5);
  text-align: left;
}

.dot .ntfs-section.active-section .description {
  text-align: left;
  padding: 0;
  margin-right: 60px;
  line-height: 25px;
  font-size: 10px;
}


.dot .alternative .title {
  font-size: 30px;
  margin-left: 5px;
}

.dot .alternative .description {
  margin: 10px 5px;
  padding: 0;
  font-size: 10px;
}

.dot .alternative .img-preview {
  grid-area: img;
  display: block;
  width: 340px;
  height: 260px;
  margin-right: 35px;
  position: relative; /* Añadir posición relativa para que la imagen pueda ajustarse correctamente */
}

.dot .alternative .img-preview img {
  object-fit: contain; /* Puedes usar 'cover' o 'contain' según tus necesidades */
  width: 100%;
  height: 100%;
  position: absolute; /* Añadir posición absoluta para ajustar la imagen al contenedor */
  top: 0;
  left: 0;
}
/*estilos para enlaces externos*/

.dot .alternative .external-links {
  justify-content: start;
}
.dot .alternative .external-links .dot-link {
}

.dot .img-preview {
  display: none;
}

/*estilos para enlaces con imagen*/
.dot .img-links-container {
}
.img-links-container .img-link {
  display: block;
  text-decoration: none;
  margin: 0 5px;
  height: 30px;

  /*TEMP STYLE*/
}
.dot .img-link img {
  max-width: 100%;
  height: 100%;
}

/* ESTILOS PARA EL CONTENEDOR DEL ICONO */
.dot .icon {
  display: none;
  padding: 10px;
  align-items: center;
  flex-direction: column;
  z-index: 100;
  pointer-events: all;
  cursor: pointer;
}

/* Estilos para el título del icono */
.dot .icon-title {
  font-size: 18px;
  text-align: center;
  max-width: 140px;
  font-weight: bold;
  margin: 0;
}

/* Estilos para el icono*/
.dot .network-sub-page .icon-img {
  height: 45px;
  width: 45px;
}

/*estilo para paginas de netwrok*/
.dot .network-sub-page-content .description {
  padding: 0 220px !important;
}

.dot .icon-img {
  height: 80px;
  width: 80px;
  margin-top: 10px;
  border-radius: 50%;
  filter: brightness(2);
  transition: transform 0.3s;
}
.icon-img:hover {
  transform: scale(1.1);
  transition: transform 0.3s;
}

/*ESTILO PARA LA NAV BAR*/
div#back-btn {
  position: fixed;
  top: 20px;
  left: 20px;
  z-index: 101;
  border: solid white 2px;
  border-radius: 50%;
  filter: brightness(2);
  width: 40px;
  height: 40px;
}
div#back-btn img {
  transform: rotate(-90deg);
  display: grid;
  object-fit: contain;
  width: 100%;
  height: 100%;
}

div#navigator {
  opacity: 0;
  /*z-index: -1;*/
}

div#navigator {
  position: absolute;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  bottom: 10px;
  left: 0;
  right: 0;
  height: 80px;
  padding: 0 35%;
  margin: 0;
}
div#navigator .icon {
  z-index: 100;
  pointer-events: all;
  transition: transform 0.3s;
}

div#navigator div.icon:hover {
  transform: scale(1.1);
  transition: transform 0.3s;
}

div#navigator div.icon img {
  width: 55px;
  height: 55px;
  pointer-events: none;
  z-index: -1;
}

div#navigator.active {
  animation: fadeIn 1s cubic-bezier(0.755, 0.05, 0.855, 0.06); /* Iniciar la animación de fade in con una curva de aceleración similar a ease-in-quint */
  animation-fill-mode: forwards;
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
