@import url(./font.css);
@import url(./dot.css);

:root {
  --color-focus: #ffffff;
  --color-medium: #b3b3b3;
}

body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  color: var(--color-focus);
  text-align: center;
  background-color: #000000;
  background-size: cover;
}

header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 80px;
  /* border-bottom: solid white 1px; */
}
header .navbar {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.navbar .logo-link{
  width: fit-content;
  display: block;
  height: 80px;
}

header .logo-link{
  z-index: 200 !important;
  cursor: pointer;
  filter: drop-shadow(0px 0px 30px white);
  transition: transform .2s;

}
header .logo-link:hover{
  transition: transform .2s;
  transform: scale(1.02);
}



canvas {
  /* display: none !important; */
}

main.intro {
  position: fixed;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 0 10vw;
  z-index: 100;

  /*display: none;*/
}

main.intro .content-container {
  display: flex;
  position: relative;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 600px;
  text-align: left;
}

h1,
h4,
p {
  margin: 0;
}

/* animations props */
.text-animated-container {
  overflow: hidden;
  /* border: solid red 1px; */
  width: 550px;
}

.animated-item {
  position: relative;
}

div.content-container .sub-title {
  font-family: Monzerrat-Regular;
  color: var(--color-medium);
  font-size: 14px;
  margin-left: 5px;
}

div.content-container .title {
  font-family: Monzerrat-Medium;
  font-size: 60px;
  line-height: 1;
  margin-top: 10px;
}

div.content-container .title.span {
  font-size: 45px;
  font-family: Monzerrat-Regular;
}

div.content-container .description {
  font-family: Monzerrat-Regular;
  line-height: 1.4;
  margin-top: 10px;
  margin-left: 5px;
  color: var(--color-medium);
}

div.content-container .start-ui {
  margin-top: 20px;
  margin-bottom: 100px;
  padding: 15px 0;
  color: var(--color-medium);
}

div.start-ui {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: 4px;
}

div#start-ui button {
  background-color: transparent;
  border: unset;
  border: solid 2px white;
  filter: brightness(2);
  transition: box-shadow 0.8s;
  color: var(--color-focus);
}

div.ui-controls button img {
  width: 20px;
  height: 20px;
}

div.start-ui button:hover {
  transition: box-shadow 0.8s;
  -webkit-box-shadow: inset 0px 0px 10px 2px #15eded8c, 0px 0px 10px 2px #15eded8c;
  -moz-box-shadow: inset 0px 0px 10px 2px #15eded8c, 0px 0px 10px 2px #15eded8c;
  box-shadow: inset 0px 0px 10px 2px #15eded8c, 0px 0px 10px 2px #15eded8c;
}

div.start-ui button.ui-title {
  font-family: Monzerrat-Regular;
  font-size: 12px;
  margin-left: 8px;
  padding: 10px 20px;
  border-radius: 35px;
}

div.start-ui div.ui-controls {
  position: fixed;
  height: 180px;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

div.start-ui button.control {
  margin-right: 0;
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

div.ui-controls button#start-scene {
  margin-right: calc(50% - 170px);
  display: grid;
  place-content: center;
}

div.ui-controls button#start-scene img {
  position: relative;
}

div.ui-controls button#start-scene::after {
  text-align: center;
  position: absolute;
  margin-left: -50px;
  margin-top: -25px;
  width: 150px;
  display: block;
  content: "CLICK TO START";
}

div.ui-controls button#toggle-sound {
  margin-right: 100px;
}
div.ui-controls button#toggle-sound .icon{
  position: absolute;
  top: 50%;
  left: 50%;
  width:  150%;
  height: 150%;
  transform: translate(-50%, -50%);
}


#blur{
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.active-blur {
  opacity: 0;
  background-color: unset;
  animation: fadeInAnimation 1s forwards cubic-bezier(0.4, 0, 0.2, 1);
}

@keyframes fadeInAnimation {
  from {
    opacity: 0;
    background-color: unset;
  }
  to {
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.55);
  }
}

